import "./home_page.css";
import { CategoriesSection, CollectionSection, HeroSection } from "./components";


export const HomePage = () => {
    
  const collection_bg_url =
    "https://sh-cdn.singulart.?signature=4a01e589d67a157e5a8512fb3743949df7d5612e96b02bd3b13ac924c1f72c9a";
  const collection_bg_url2 =
    "https://sh-cdn.singulart.?signature=ce6e4956bbf7bd80c4bf6471ec041536f8543a032bd9c053fcc96ecc80c63b1c";
  const category_list = [
    "Abstraction",
    "Nature",
    "Landscape",
    "urban",
    "Street",
    "Medival",
    "Pop Culture",
    "Portrait",
  ];
  return (
    <main className="main">
      {/*  hero section */}
      <HeroSection/>

      {/* category section */}
      <CategoriesSection category_list={category_list}/>

      {/* collection section  */}
      <CollectionSection collection_bg_url2={collection_bg_url2} collection_bg_url={collection_bg_url}/>
    </main>
  );
};
